@font-face {
  font-family: 'GT America Regular';
  src: url('/public/fonts/GT\ America\ Regular.otf');
  /* IE9 Compat Modes */
  src: url('/public/fonts/GT\ America\ Regular.ttf') format('truetype');
  /* Safari, Android, iOS */
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'GT America Bold';
  src: url('/public/fonts/GT\ America\ Bold.otf');
  /* IE9 Compat Modes */
  src: url('/public/fonts/GT\ America\ Bold.ttf') format('truetype');
  /* Safari, Android, iOS */
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Dot Matrix';
  src: url('/public/fonts/Dot_Matrix.ttf');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.card-hover {
  overflow: hidden !important;
}

.img-card {
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.img-card:hover {
  transform: scale(1.1);
}

.scrollX ::-webkit-scrollbar {
  display: none !important;
}

.scrollX {
  -ms-overflow-style: none !important;
  scrollbar-width: 0 !important;
}

input:focus {
  outline: none !important;
  box-shadow: none !important;
  outline-offset: 0 !important;
}

.invert-filter {
  filter: invert(100%);
  -webkit-filter: invert(100%);
}

.phoneInput {
  background-color: #61dafb;
}


.followFlex .unfollow {
  display: none;
}

.followFlex:hover .following {
  display: none;
}

.followFlex:hover .unfollow {
  display: flex;
}


.banner-cards::before {
  content: "";
  position: absolute;
  background: rgba(255, 255, 255, 0.1);
  top: 0;
  height: 100%;
  left: 0px;
}

.banner-cards::after {
  content: "";
  position: absolute;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  top: 0;
  height: 100%;
  width: 100%;
  left: -100%;
  transition: 2s;
}

.banner-cards:hover::after {
  left: 100%;
}


.dash-cards::before {
  /* content: "";
  position: absolute;
  background-color: rgba(255, 255, 255, 0.3);
  top: 0;
  height: 140px;
  width: 30px;
  left: -80px;*/
  transform: skewX(30deg);

  content: "";
  position: absolute;
  background: rgba(255, 255, 255, 0.1);
  top: 0;
  height: 100%;
  left: 0px;

}

.dash-cards::after {
  content: "";
  position: absolute;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  top: 0;
  height: 100%;
  width: 100%;
  left: -100%;
  transition: 1s;
}


.dash-cards:hover::after {
  /* animation: slide 1s; */
  left: 100%;
}



.hero-container-a {
  background-color: #FFF;
  width: 100%;
  height: 750px;
  background-image: linear-gradient(60deg, #FFF, #FFF, #F482FB, #F482FB, #7394F6, #44A134, #C0CB3D, #FFFFFF, #FFFFFF);
  background-size: 600% 600%;
  animation: bgAnimatedGrad 35s infinite linear;
}

.hero-container-b {
  background-color: #FFF;
  width: 100%;
  height: 750px;
  background-image: linear-gradient(60deg, #0B0325, #0B0325, #0B0325, #36215D, #512EA5, #2800AC, #5694C1, #7F38D8, #7F38D8);
  background-size: 600% 600%;
  animation: bgAnimatedGrad 35s infinite linear;
}

.hero-container-c {
  background-color: #FFF;
  width: 100%;
  height: 750px;
  background-image: linear-gradient(50deg, #000, #000, #340713, #5D1B91, #1C237C, #C9887A, #AC44AE, #AC44AE);
  background-size: 600% 600%;
  animation: bgAnimatedGrad 35s infinite linear;
}

@keyframes bgAnimatedGrad {
  0% {
    background-position: 0% 85%;
  }

  50% {
    background-position: 100% 20%;
  }

  100% {
    background-position: 0% 85%;
  }
}

.infinite-bounce {
  position: relative;
  animation: bounce 1.5s linear infinite;
}

@keyframes bounce {
  0% {
    transform: translateY(0px);
  }

  10% {
    transform: translateY(-20px);
  }

  20% {
    transform: translateY(0px);
  }

  25% {
    transform: translateY(-10px);
  }

  35% {
    transform: translateY(0px);
  }

  45% {
    transform: translateY(-5px);
  }

  100% {
    transform: translateY(0px);
  }
}

@keyframes slide {
  0% {
    left: -80px;
  }

  100% {
    left: 120%;
  }
}

@keyframes bannerSlides {
  0% {
    left: -80px;
  }

  100% {
    left: 130%;
  }
}